// material
import { styled } from '@mui/material/styles';
import { Toolbar, OutlinedInput, InputAdornment } from '@mui/material';
// icons
import SearchIcon from '@mui/icons-material/Search';



// ----------------------------------------------------------------------

const RootStyle = styled(Toolbar)(({ theme }) => ({
  height: 96,
  display: 'flex',
  justifyContent: 'space-between',
  padding: theme.spacing(0, 1, 0, 0),
}));

const SearchStyle = styled(OutlinedInput)(({ theme }) => ({
  width: 300,
  transition: theme.transitions.create(['box-shadow', 'width'], {
    easing: theme.transitions.easing.easeInOut,
    duration: theme.transitions.duration.shorter,
  }),
  '&.Mui-focused': { width: 400, boxShadow: 200 },
  '& fieldset': {
    borderWidth: `1px !important`,
    borderColor: `0px 5px 6px -3px alpha(color, 0.2),0px 9px 12px 1px alpha(color, 0.14),0px 3px 16px 2px alpha(color, 0.12) !important`,
  },
}));

// ----------------------------------------------------------------------

type StudentToolbar = {
    filterName: string,
    onFilterName: (event: React.ChangeEvent<HTMLInputElement>) => void,
}

export default function StudentListToolbar({ filterName, onFilterName }: StudentToolbar) {
  return (
    <RootStyle>
        <SearchStyle
            value={filterName}
            onChange={onFilterName}
            type="number"
            placeholder="Search student number..."
            startAdornment={
            <InputAdornment position="start">
                <SearchIcon />
            </InputAdornment>
        } />
    </RootStyle>
  );
}