import { useState, useEffect } from "react";
//components
import AccountToolbar from "./AccountToolbar";
import StudentListHead from "./StudentListHead";
import EditAccount from "./EditAccount";
import Confirmation from "./Confirmation";
//mui material \
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Card from "@mui/material/Card";
import TableContainer from "@mui/material/TableContainer";
import Table from "@mui/material/Table";
import TableRow from "@mui/material/TableRow";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TablePagination from "@mui/material/TablePagination";
import Avatar from "@mui/material/Avatar";
import Stack from "@mui/material/Stack";
import IconButton from "@mui/material/IconButton";
import Tooltip from "@mui/material/Tooltip";
//utils
import { capitalCase } from 'change-case';
import { Accounts, GetAllAccounts } from "../utils/GetRequests";
//Icons
import EditIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';

const headLabels = [
    {
        id: 'avatar',
        label: '',
        alignRight: false
      },
    {
      id: 'first_name',
      label: 'First Name',
      alignRight: false
    },
    {
      id: 'middle_name',
      label: 'Middle Name.',
      alignRight: false
    },
    {
      id: 'last_name',
      label: 'Last Name',
      alignRight: false
    },
    {
      id: 'student_id',
      label: 'Student No.',
      alignRight: false
    },
    {
      id: 'course',
      label: 'Course',
      alignRight: false
    },
    {
      id: 'options',
      label: '',
      alignRight: true
    }
  ];

export default function AccountsRecord() {
    const [accounts, setAccounts] = useState<Accounts[]>([]);
    const [filteredAcc, setFilteredAcc] = useState<Accounts[]>([]);
    const [rowsPerPage, setRowsPerPage] = useState<number>(5);
    const [page, setPage] = useState<number>(0);
    const [open, setOpen] = useState<boolean>(false);
    const [selected, setSelected] = useState<Accounts>();
    const [filterName, setFilterName] = useState<string>("");
    const [confirm, setConfirm] = useState<boolean>(false);

    useEffect(() => {
        const getLogs = async () => {
          try {
            const logs = await GetAllAccounts();
            setAccounts(state => logs);
          } catch (error) {
            console.log(error);
          }
        }
    
        getLogs();
    }, []);

    useEffect(() => {
        setFilteredAcc(state => accounts.filter(log => log.stud_number.includes(filterName)).slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage));
    }, [rowsPerPage, page, accounts, filterName]);

    const handlePageChange  = (event: React.MouseEvent<HTMLButtonElement, MouseEvent> | null, page: number) => setPage(page);

    const handleRowPerPageChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    }

    const handleEditDialog = (value: Accounts) => {
        setSelected(value);
        setOpen(true);
    }

    const updateLogs = async () => {
        setOpen(false);
        setConfirm(false);
        try {
          const logs = await GetAllAccounts();
          setAccounts(logs);
        } catch (error) {
          console.log(error);
        }
    }

    const handleDeleteDialog = (value: Accounts) => {
        setSelected(value);
        setConfirm(true);
    }

    return (
        <Card sx={{ p: 2 }}>
        <AccountToolbar filterName={filterName} onFilterName={(event) => setFilterName(event.target.value)} />
        <Box sx={{ overflowX: 'auto' }}>
            <TableContainer sx={{ minWidth: 1000 }}>
            <Table>
                <StudentListHead orderBy='name' order='asc' headLabel={headLabels} onRequestSort={(property) => {}} />
                <TableBody>
                {filteredAcc.map(log => (
                    <TableRow
                        hover
                        key={log.stud_number}
                        tabIndex={-1}
                        role="checkbox"
                    >
                        <TableCell padding="checkbox">
                        <Avatar>{log.first_name.charAt(0).toUpperCase() + log.last_name.charAt(0).toUpperCase()}</Avatar>
                        </TableCell>
                        <TableCell component="th" scope="row">
                            <Typography variant="subtitle2" noWrap>
                                {capitalCase(log.first_name)}
                            </Typography>
                        </TableCell>
                        <TableCell component="th" scope="row">
                            <Typography variant="subtitle2" noWrap>
                                {capitalCase(log.middle_name)}
                            </Typography>
                        </TableCell>
                        <TableCell component="th" scope="row">
                            <Typography variant="subtitle2" noWrap>
                                {capitalCase(log.last_name)}
                            </Typography>
                        </TableCell>
                        <TableCell align="left">{log.stud_number}</TableCell>
                        <TableCell align="left">{log.section}</TableCell>
                        <TableCell align="right">
                            <Stack direction="row" justifyContent="flex-end">
                                <IconButton color="info" onClick={() => handleEditDialog(log)}>
                                    <Tooltip title="Edit">
                                        <EditIcon />
                                    </Tooltip>
                                </IconButton>
                                <IconButton color="error" onClick={() => handleDeleteDialog(log)}>
                                    <Tooltip title="Delete">
                                        <DeleteIcon />
                                    </Tooltip>
                                </IconButton>
                            </Stack>
                        </TableCell>
                    </TableRow>
                ))}
                </TableBody>
            </Table>
            </TableContainer>
        </Box>
        <TablePagination
            rowsPerPageOptions={[5, 10, 25]}
            component="div"
            count={accounts.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handlePageChange}
            onRowsPerPageChange={handleRowPerPageChange}
        />
        {selected && (
            <EditAccount account={selected} open={open} handleClose={updateLogs} />
        )}
       
        {selected && (
            <Confirmation name={selected.first_name + " " + selected.last_name} serial={selected.serial} open={confirm} handleClose={updateLogs} />
        )}
        </Card>
    )
}