import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
// Utils
import axios from 'axios';
import { capitalCase } from "change-case";

type AlertProps = {
    open: boolean;
    handleClose: () => void;
    name: string;
    serial: string;
}

export default function AlertDialog(props: AlertProps) {
    const { open, name, serial, handleClose } = props;

    const deleteUser = () => {
        axios.post(`${process.env.REACT_APP_BACKEND_URL}/students/delete`, JSON.stringify({
            serial: serial
        }), { headers: { 'Content-Type': 'application/json' }})
        .then(response => {
            handleClose();
        }).catch(error => console.log(error));
    }
 
    return (
        <Dialog
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        >
        <DialogTitle id="alert-dialog-title">
            {"Are you sure you want to delete " + capitalCase(name)}
        </DialogTitle>
        <DialogContent>
            <DialogContentText id="alert-dialog-description">
                {`Please keep in mind that by clicking 'Yes', all ${capitalCase(name)}'s logs will also be deleted and cannot be retrieved again.`}
            </DialogContentText>
        </DialogContent>
        <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button onClick={deleteUser} autoFocus>
                Yes
            </Button>
        </DialogActions>
        </Dialog>
    );
}
