import React, { useState } from 'react';
import Grid from '@mui/material/Grid';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Stack from '@mui/material/Stack';
import Avatar from '@mui/material/Avatar';
//utils
import axios from "axios";
// storage
import { ref, uploadBytes, getDownloadURL } from "firebase/storage";
import { firebaseStorage } from "../utils/FirebaseProvider";

type DialogProps = {
    open: boolean;
    handleClose: () => void;
    account: AccountRecord;
}

interface AccountRecord extends StudentRecord {
    serial: string;
}

type StudentRecord = {
    stud_number: string;
    first_name: string;
    last_name: string;
    middle_name: string;
    section: string;
    parent_email: string;
    photo_url?: string;
}

const hasNumber = (str: string) => /\d/.test(str);

function RegisterStudent(props: DialogProps) {
  const { open, account, handleClose } = props;
  const [student, setStudent] = useState<StudentRecord>({
    stud_number: account.stud_number,
    first_name: account.first_name,
    last_name: account.last_name,
    middle_name: account.middle_name,
    section: account.section,
    parent_email: account.parent_email
  });
  const [error, setError] = useState<string>();
  const [photoFile, setPhotoFile] = useState<File | null>(null);
  const [photoSource, setPhotoSource] = useState<(string | ArrayBuffer | null)[]>();
  const { stud_number, first_name, last_name, middle_name, section, parent_email } = student;

    React.useEffect(() => {
        setStudent(state => ({
            stud_number: account.stud_number,
            first_name: account.first_name,
            last_name: account.last_name,
            middle_name: account.middle_name,
            section: account.section,
            parent_email: account.parent_email
        }))
    }, [account])

    React.useEffect(() => {
        if (photoFile !== null) {
            let reader = new FileReader();
            reader.readAsDataURL(photoFile);

            reader.onloadend = function (e) {
                setPhotoSource([reader.result]);
            };

        }
    }, [photoFile])

  const handleTextChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (!hasNumber(event.target.value)) {
      setStudent({ ...student, [event.target.name]: event.target.value });
    }
  }

  const handleNumberChange = (event: React.ChangeEvent<HTMLInputElement>) => setStudent({ ...student, [event.target.name]: event.target.value });

  const handleCourseChange = (event: React.ChangeEvent<HTMLInputElement>) => {
      setStudent({ ...student, [event.target.name]: event.target.value.toUpperCase() });
  }

  const handleChangePhoto = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files) setPhotoFile(e.target.files[0])
  }

  const handleUploadPhoto = async () => {
    if (photoFile) {
        const storageRef = ref(firebaseStorage, "profiles/" + photoFile.name);
        await uploadBytes(storageRef, photoFile);

        return await getDownloadURL(storageRef);
    }

    return null;
  }

  const handleSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();

    const photoURL = await handleUploadPhoto();

    if (photoURL || account.photo_url) {
        axios.post(`${process.env.REACT_APP_BACKEND_URL}/students/update`, JSON.stringify({
            first_name: first_name,
            last_name: last_name,
            middle_name: middle_name,
            section: section,
            serial: account.serial,
            photo_url: photoURL,
            parent_email: parent_email
        }), { headers: { 'Content-Type': 'application/json' }})
        .then(response => {
            setStudent({
                stud_number: '',
                first_name: '',
                last_name: '',
                middle_name: '',
                section: '',
                parent_email: ''
            });
            setError(undefined);
            handleClose();
        }).catch(error => {
            console.log(error);
            setError("Please check your Internet connection and try again.");
        });
    }
  }

  return (
    <Dialog open={open} onClose={handleClose}>
        <form onSubmit={handleSubmit}>
            <DialogTitle>Update Account</DialogTitle>
            <DialogContent>
                <Grid container spacing={2} mt={3}>
                    <Grid item xs={12} sm={6}>
                        <Stack 
                            direction="column" 
                            justifyContent="center" 
                            alignItems="center"
                            spacing={2}
                        >
                            <TextField
                                name="serial"
                                label="Serial Number"
                                value={account.serial}
                                fullWidth
                                variant="outlined"
                            />
                            <TextField
                                autoFocus
                                name="first_name"
                                label="First Name"
                                value={first_name}
                                onChange={handleTextChange}
                                inputProps={{ maxLength: 20 }}
                                fullWidth
                                variant="outlined"
                                required
                            />
                        </Stack>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Stack justifyContent="center" alignItems="center" spacing={2}>
                            <Avatar src={photoSource as any || account.photo_url} sx={{ height: 90, width: 90, cursor: 'pointer' }}>NA</Avatar>
                            <Button
                                variant="outlined"
                                size='small'
                                component="label"
                            >
                                Change Image
                                <input 
                                    type="file" 
                                    accept="image/png, image/jpeg, image/jpg"
                                    onChange={handleChangePhoto}
                                    hidden 
                                />
                            </Button>
                        </Stack>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="middle_name"
                            label="Middle Name"
                            value={middle_name}
                            inputProps={{ maxLength: 20 }}
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="stud_number"
                            label="Student Number"
                            type="number"
                            value={stud_number}
                            inputProps={{ min: 1111111111, max: 99999999999 }}
                            onChange={handleNumberChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="last_name"
                            label="Last Name"
                            value={last_name}
                            inputProps={{ maxLength: 20 }}
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            name="section"
                            label={"Course & Section"}
                            value={section}
                            onChange={handleCourseChange}
                            inputProps={{ maxLength: 6 }}
                            fullWidth
                            variant="outlined"
                            required
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            name="parent_email"
                            label="Parent Email"
                            value={parent_email}
                            type="email"
                            onChange={handleTextChange}
                            fullWidth
                            variant="outlined"
                            required
                            error={error !== undefined}
                            helperText={error}
                        />
                    </Grid>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button color="error" onClick={handleClose}>Cancel</Button>
                <Button type="submit">Save Changes</Button>
            </DialogActions>
        </form>
    </Dialog>
  )
}

export default RegisterStudent